import { reactive, computed, ref } from 'vue'
import store from 'store'
import { isFunction } from 'utils/is'
import { formatDatabase } from 'utils/date'
import { mapTabRoute } from 'utils/mapping'
import { anchorDownload } from 'utils/element'
import { fileFormat } from 'utils/format'
import { useUser } from 'composable/user'
import moment from 'moment'

const useStatuses = () => {
  const state = computed(() => {
    return store.state.customer.statuses
  })
  const tab = computed(() => {
    return state.value.map(item => mapTabRoute(item, 'customerTab', {
      statusId: item.id
    }, 'id', 'short_name'))
  })

  return {
    state,
    tab
  }
}

const useCustomers = () => {
  const { me, dateByTimezone } = useUser()

  const blockedStatus = [
    { type: 'ip', name: 'IP Address' },
    { type: 'email', name: 'Email' },
    { type: 'domain', name: 'Domain' }
  ]
  const data = reactive({
    page: 1,
    limit: 10,
    search: '',
    sort_by: '',
    order_by: '',
    company: null,
    name: null,
    last_seen: null,
    from: null,
    to: null,
    date: null,
    status: 1,
    type: null
  })
  const loading = reactive({
    all: false,
    delete: false,
    restore: false,
    csv: false
  })
  const modal = reactive({
    delete: false,
    restore: false
  })
  const selected = ref(null)
  const filterDateRef = ref(null)
  const filterDateOptions = reactive({
    disabledDate(time) {
      return time.getTime() > dateByTimezone.value.getTime()
    }
  })
  const  isDateShow = ref(false)
  const filterDateFocus = () => {
    if (isDateShow.value) {
      isDateShow.value = false
      return
    }

    isDateShow.value = true
    filterDateRef.value.focus() 
  }

  const filterList = computed(() => {
    const general = [{
      id: 'date',
      name: 'Date',
      value: data.date ? `${moment(data.date[0]).format(me.value?.date_format || '')} - ${moment(data.date[1]).format(me.value?.date_format || '')}` : null,
      default: null
    }]
    const blocked = [{
      id: 'type',
      name: 'Blocked Type',
      value: blockedStatus.find(i => i?.type === data?.type) ?? null,
      default: null
    }]
    return data.status == 2 ? [...general, ...blocked] : general
  })
  const isFiltered = computed(() => {
    return filterList.value.filter(filter => filter.value).length > 0
  })

  const clearFilter = (filter) => {
    if( !data.hasOwnProperty(filter.id) ) return false
    data[filter.id] = filter.default
  }
  const clearAllFilter = () => {
    for( let filter of filterList.value ){
      data[filter.id] = filter.default
    }
  }
  const unsetData = () => {
    selected.value = null
  }
  const openModal = (target, callback = () => {}) => {
    modal[target] = true
    isFunction(callback) && callback()
  }
  const actionAll = async (statusId = null, callback = () => {}) => {
    loading.all = true
    try {
      data.status = statusId
      const response = await store.dispatch('customer/all', {
        ...data,
        type: statusId == 2 ? data.type : undefined,
        status: statusId,
        date: null,
        from: data.date && formatDatabase(data.date[0]) || null,
        to: data.date && formatDatabase(data.date[1]) || null
      })
      isFunction(callback) && callback()
      loading.all = false
    } catch (error) {
      if (error?.message !== 'canceled') {
        store.commit('customer/SET_ALL', {
          ...store.state.customer.all,
          data: []
        }, { root: true })
        loading.all = false
      }
    }
  }
  const actionDelete = async (statusId, callback = () => {}) => {
    try {
      loading.delete = true
      await store.dispatch(`customer/delete`, selected.value)
      isFunction(callback) && callback()
      modal.delete = false
      unsetData()
      await actionAll(statusId)
    }
    finally {
      loading.delete = false
    }
  }
  const actionRestore = async (statusId, callback = () => {}) => {
    try {
      loading.restore = true
      await store.dispatch(`customer/restore`, selected.value)
      isFunction(callback) && callback()
      modal.restore = false
      unsetData()
      await actionAll(statusId)
    }
    finally {
      loading.restore = false
    }
  }

  const getCsv = async(options={}, callback = () => {}) => {
    try {
      const payload = {
        extension: 'csv'
      }
      const { format, filters, filename } = options
      loading.csv = true
      Object.assign(payload, filters)
      if(format) payload.extension = format
      const response = await store.dispatch(`customer/actionCsv`, payload)
      isFunction(callback) && callback()
      anchorDownload(response, filename || 'customer-csv', fileFormat(format))
      return response
    }
    finally {
      loading.csv = false
    }
  }
  const state = computed(() => {
    const customers = store?.state?.customer?.all ? Object.assign({}, {
      ...store.state.customer.all
    }) : {}

    return customers
  })

  return {
    data,
    state,
    loading,
    modal,
    selected,
    filterList,
    isFiltered,
    filterDateRef,
    filterDateOptions,
    isDateShow,
    blockedStatus,
    filterDateFocus,
    clearFilter,
    clearAllFilter,
    unsetData,
    openModal,
    actionAll,
    actionDelete,
    actionRestore,
    getCsv
  }
}

const useCreate = () => {
  const data = reactive({
    first_name: null,
    last_name: null,
    email: null,
    use_company_name: false,
    company: null,
    address1: null,
    city: null,
    state: null,
    zip: null,
    country: null,
    address_text: null,
    phone: null,
    suite: '',
    license_number: null
  })
  const loading = ref(false)
  const action = async (callback = () => {}) => {
    try {
      loading.value = true
      const response = await store.dispatch(`customer/create`, {
        ...data,
        customer_country: data.country
      })
      isFunction(callback) && callback(response)
    }
    finally {
      loading.value = false
    }
  }

  return {
    data,
    loading,
    action
  }
}

const useUpdate = () => {
  const loading = ref(false)
  const action = async (customerId, data, callback = () => {}) => {
    try {
      loading.value = true
      const response = await store.dispatch(`customer/update`, {
        id: customerId,
        data: {
          ...data,
          customer_country: data.country
        }
      })
      isFunction(callback) && callback(response)
    }
    finally {
      loading.value = false
    }
  }

  return {
    loading,
    action
  }
}

const usePaymentMethods = buyback => {
  const loading = ref(false)
  const action = async (customerId, callback = () => {}) => {
    loading.value = true
    if( buyback === 'online' ){
      await store.dispatch('customer/onlinePaymentMethods', customerId)
    }else{
      await store.dispatch(`customer/paymentMethods`, customerId)
    }
    isFunction(callback) && callback()
    loading.value = false
  }
  const state = computed(() => {
    return store.state.customer.paymentMethods
  })

  return {
    state,
    loading,
    action
  }
}

const useBuyback = () => {
  const { me, dateByTimezone } = useUser()
  const data = reactive({
    page: 1,
    limit: 10,
    search: '',
    payment_method: [],
    from: null,
    to: null,
    sort_by: '',
    order_by: '',
    date: null,
    shipping_courier: [],
    statuses: []
  })
  const filterList = computed(() => {
    return [{
      id: 'payment_method',
      name: 'Payout Method',
      value: data.payment_method ? data.payment_method.join(', ') : null,
      default: []
    },{
      id: 'date',
      name: 'Date',
      value: data.date ? `${moment(data.date[0]).format(me.value?.date_format || '')} - ${moment(data.date[1]).format(me.value?.date_format || '')}` : null,
      default: null
    },{
      id: 'shipping_courier',
      name: 'Courier',
      value: data.shipping_courier ? data.shipping_courier.join(', ') : null,
      default: []
    },{
      id: 'statuses',
      name: 'Status',
      value: data.statuses ? data.statuses.map(status => status.status_name).join(', ') : null,
      default: []
    }]
  })
  const isFiltered = computed(() => {
    return filterList.value.filter(filter => filter.value).length > 0
  })
  const clearFilter = (filter) => {
    if( !data.hasOwnProperty(filter.id) ) return false
    data[filter.id] = filter.default
  }
  const clearAllFilter = () => {
    for( let filter of filterList.value ){
      data[filter.id] = filter.default
    }
  }
  const filterDateRef = ref(null)
  const filterDateOptions = reactive({
    disabledDate(time) {
      return time.getTime() > dateByTimezone.value.getTime()
    },
    // shortcuts: [
    //   {
    //     text: 'A Week Ago',
    //     value: [new Date(Date.now() - date().weekAgo), new Date()]
    //   },
    //   {
    //     text: 'A Month Ago',
    //     value: [new Date(Date.now() - date().monthAgo), new Date()]
    //   },
    //   {
    //     text: '2 Month Ago',
    //     value: [new Date(Date.now() - date().monthAgo * 2), new Date()]
    //   },
    //   {
    //     text: '3 Month Ago',
    //     value: [new Date(Date.now() - date().monthAgo * 3), new Date()]
    //   }
    // ]
  })
  let isDateShow = false
  const filterDateFocus = () => {
    if (isDateShow) {
      isDateShow = false
      return
    }

    isDateShow = true
    filterDateRef.value.focus()
  }
  const loading = ref(false)
  const action = async (id, isInStore, callback = () => {}) => {
    loading.value = true
    const tempData = Object.assign({}, data)
    tempData.from = tempData.date && formatDatabase(tempData.date[0]) || null
    tempData.to = tempData.date && formatDatabase(tempData.date[1]) || null
    Object.assign(tempData, {
      is_in_store: isInStore ? 1 : 0
    })
    Object.assign(tempData, {
      status_id: data.statuses ? data.statuses.map(status => status.id) : null
    })
    delete tempData.date
    await store.dispatch(`customer/detailOrders`, {
      id,
      data: tempData
    })
    isFunction(callback) && callback()
    loading.value = false
  }
  const tab = computed(() => {
    return [{
        name: 'In-Store',
        category: 'in-store'
      },
      {
        name: 'Mail-In',
        category: 'mail-in'
      },
      {
        name: 'Invoice',
        category: 'invoice'
      }
    ].map(menu => mapTabRoute(menu, 'customerDetail', {category: menu.category}, 'category', 'name'))
  })
  const state = computed(() => {
    return store.state.customer.detailOrders
  })

  return {
    tab,
    data,
    state,
    loading,
    filterDateRef,
    filterDateOptions,
    filterList,
    isFiltered,
    clearFilter,
    clearAllFilter,
    filterDateFocus,
    action
  }
}

const useInvoice = () => {
  const { me, dateByTimezone } = useUser()
  const data = reactive({
    page: 1,
    limit: 10,
    search: '',
    from: null,
    to: null,
    sort_by: '',
    order_by: '',
    status_id: 'all',
    date: null,
    sales_person: [],
  })

  const invoiceStatuses = computed(() => {
    return store.state.buyback.invoice.statuses.map((status) => ({id: status.id, name: status.short_name}))
  })

  const filterList = computed(() => {
    return [{
      id: 'sales_person',
      name: 'Sales Person',
      value: data.sales_person.length > 0 ? data.sales_person.map(sales => sales.sales_person).join(', ') : null,
      default: []
    },{
      id: 'date',
      name: 'Date',
      value: data.date ? `${moment(data.date[0]).format(me.value?.date_format || '')} - ${moment(data.date[1]).format(me.value?.date_format || '')}` : null,
      default: null
    },{
      id: 'status_id',
      name: 'Status',
      value: data.status_id ? invoiceStatuses.value.find((status) => status.id == data.status_id)?.name : 'all',
      default: 'all'
    }]
  })
  const isFiltered = computed(() => {
    return filterList.value.filter(filter => filter.value).length > 0
  })
  const clearFilter = (filter) => {
    if( !data.hasOwnProperty(filter.id) ) return false
    data[filter.id] = filter.default
  }
  const clearAllFilter = () => {
    for( let filter of filterList.value ){
      data[filter.id] = filter.default
    }
  }

  const filterDateRef = ref(null)
  const filterDateOptions = reactive({
    disabledDate(time) {
      return time.getTime() > dateByTimezone.value.getTime()
    }
  })
  let isDateShow = false
  const filterDateFocus = () => {
    if (isDateShow) {
      isDateShow = false
      return
    }

    isDateShow = true
    filterDateRef.value.focus()
  }

  const loading = ref(false) 
  const loadingSalesPerson = ref(false) 

  const action = async (customerId, callback = () => {}) => {
    loading.value = true
    const tempData = Object.assign({}, data)
    tempData.from = tempData.date && formatDatabase(tempData.date[0]) || null
    tempData.to = tempData.date && formatDatabase(tempData.date[1]) || null
    tempData.sales_person = tempData.sales_person.map(sales => sales.id)
    delete tempData.date

    await store.dispatch('customer/showInvoice', {id: customerId, data: tempData})
    isFunction(callback) && callback()
    loading.value = false
  }

  const actionSalesPerson = async (payload, callback = () => {}) => {
    loadingSalesPerson.value = true
    await store.dispatch('customer/showSalesPerson')
    isFunction(callback) && callback()
    loadingSalesPerson.value = false
  }

  const state = computed(() => {
    return store.state.customer.invoices
  })

  const stateSalesPerson = computed(() => {
    return store.state.customer.sales_persons
  })

  return {
    data,
    loading,
    loadingSalesPerson,
    state,
    stateSalesPerson,
    filterDateRef,
    filterDateOptions,
    invoiceStatuses,
    filterList,
    isFiltered,
    action,
    actionSalesPerson,
    filterDateFocus,
    clearFilter,
    clearAllFilter
  }
}

const useShow = () => {
  const loading = ref(false)
  const data = ref(null)
  const action = async (customerId, callback = () => {}) => {
    loading.value = true
    data.value = await store.dispatch(`customer/show`, customerId)
    isFunction(callback) && callback()
    loading.value = false
  }
  const state = computed(() => {
    return store.state.customer.show.data
  })

  return {
    data,
    state,
    loading,
    action
  }
}

const useShowPaymentInfo = () => {
  const loading = ref(false)
  const data = ref(null)
  const action = async (customerId, callback = () => {}) => {
    loading.value = true
    data.value = await Promise.all([
      store.dispatch(`customer/showPaymentInfo`, customerId),
      store.dispatch(`customer/showPaymentInfoBanks`, customerId)
    ]).then(response => response[0])
    try {
      store.dispatch(`customer/showPaymentInfoCustom`, customerId)
    } catch (error) {
      
    }
    isFunction(callback) && callback()
    loading.value = false
  }
  const actionUpdate = async(customerId, payload={}, callback = () => {}) => {
    loading.value = true
    loading.value = true
    await store.dispatch('customer/updatePaymentInfo', {
      id: customerId,
      data: payload
    })
    isFunction(callback) && callback()
    loading.value = false
  }
  const actionUpdateCustom = async(customerId, payload={}, callback = () => {}) => {
    loading.value = true
    try {
      await store.dispatch('customer/showPaymentInfoCustomUpdate', {
        id: customerId,
        data: payload
      })
      isFunction(callback) && callback()
    } catch (error) {
      console.error(error)
    } finally {
      loading.value = false
    }
  }
  const state = computed(() => {
    return store.state.customer.show.paymentInfo
  })

  return {
    data,
    state,
    loading,
    action,
    actionUpdate,
    actionUpdateCustom
  }
}

const useShowPaymentInfoBankTransfer = () => {
  const data = reactive({})
  const modal = reactive({
    form: false,
    delete: false
  })
  const loading = reactive({
    all: false,
    form: false,
    delete: false
  })
  const selected = ref(null)
  const openModal = (target, callback = () => {}) => {
    modal[target] = true
    isFunction(callback) && callback()
  }
  const unsetData = () => {
    selected.value = null
    for(const [attributeKey] of Object.entries(data)) delete data[attributeKey]
  }
  const actionAll = async (customerId, callback = () => {}) => {
    loading.all = true
    await store.dispatch(`customer/showPaymentInfoBanks`, customerId)
    isFunction(callback) && callback()
    loading.all = false
  }
  const actionCreate = async (customerId, callback = () => {}) => {
    try {
      loading.form = true
      await store.dispatch(`customer/showPaymentInfoBankCreate`, {
        id: customerId,
        data
      })
      isFunction(callback) && callback()
      modal.form = false
      unsetData()
      await actionAll(customerId)
    }
    finally {
      loading.form = false
    }
  }
  const actionUpdate = async (customerId, callback = () => {}) => {
    try {
      loading.form = true
      await store.dispatch(`customer/showPaymentInfoBankUpdate`, {
        id: customerId,
        bank_id: selected.value,
        data
      })
      isFunction(callback) && callback()
      modal.form = false
      unsetData()
      await actionAll(customerId)
    } finally {
      loading.form = false
    }
  }
  const actionDelete = async (customerId, callback = () => {}) => {
    try {
      loading.delete = true
      await store.dispatch(`customer/showPaymentInfoBankDelete`, selected.value)
      isFunction(callback) && callback()
      modal.delete = false
      unsetData()
      await actionAll(customerId)
    }
    finally {
      loading.delete = false
    }
  }

  return {
    data,
    loading,
    modal,
    selected,
    openModal,
    unsetData,
    actionAll,
    actionCreate,
    actionUpdate,
    actionDelete
  }
}

export {
  useStatuses,
  useCustomers,
  useCreate,
  useUpdate,
  usePaymentMethods,
  useBuyback,
  useInvoice,
  useShow,
  useShowPaymentInfo,
  useShowPaymentInfoBankTransfer
}